import React, { Component } from 'react'
import {
    Container,
    Form,
    FormGroup,
    Label,
    CustomInput,
    Card,
    Button,
    Col,
    Row
} from 'reactstrap';


export default class Filter extends Component {

    state = {
        checkBoxesFilter: [],
        multipleSelectFilter: [],

    }

    handleClearFilterClick = (event) => {
        this.setState({ checkBoxesFilter: [], multipleSelectFilter: [] })
        this.props.onClearFilter();
    }

    handleApplyFilterClick = (event) => {
        this.props.onFilter(this.state.checkBoxesFilter, this.state.multipleSelectFilter)
    }

    handleCheckBoxesChange = (event) => {


        this.state.checkBoxesFilter.push(event.target.value)
        if (event.target.value !== '') {
            event.target.value = '';
        }
        else {
            event.target.value = event.target.value;
        }

    }


    handleMultipleSelectChange = (event) => {

        let newMultipleSelectFilter = [];
        let option;
        for (let i = 0, len = event.target.options.length; i < len; i++) {
            option = event.target.options[i];

            if (option.selected) {
                newMultipleSelectFilter.push(option.value);
            }
        }

        this.setState({multipleSelectFilter: newMultipleSelectFilter })

    }

    render() {

        function onlyUnique(value, index, self) {
            return self.indexOf(value) === index;
        }


        const users = this.props.products.map((element, index) => {
            return <option>{element.user}</option>
        });

        const allTags = [];
        this.props.products.map((element, index) => {
            element.tag.forEach(item => {
                allTags.push(item)
            });
        });
        var uniqueTags = allTags.filter(onlyUnique);

        const tagList = uniqueTags.map((tag, index) => {
            return <option>{tag}</option>
        });


        return (
            <div>
                <Card className="p-2" color="light" style={{ textAlign: 'left' }}>
                    <Form >

                        <FormGroup>
                            <Label for="exampleCheckbox1" ><strong>By Category</strong></Label>
                            <div>
                                <CustomInput type="checkbox" id="exampleCustomCheckbox" label="Food" onChange={this.handleCheckBoxesChange} value="food" />
                                <CustomInput type="checkbox" id="exampleCustomCheckbox2" label="Accessories" onChange={this.handleCheckBoxesChange} value="accessories" />
                                <CustomInput type="checkbox" id="exampleCustomCheckbox3" label="Clothing" onChange={this.handleCheckBoxesChange} value="clothing" />
                                <CustomInput type="checkbox" id="exampleCustomCheckbox4" label="Toys" onChange={this.handleCheckBoxesChange} value="toys" />
                            </div>
                        </FormGroup>

                        <FormGroup>
                            <Label for="exampleCheckbox2"><strong>By Location</strong></Label>
                            <div>
                                <CustomInput type="checkbox" id="exampleCustomCheckbox5" label="Riyadh" onChange={this.handleCheckBoxesChange} value="riyadh" />
                                <CustomInput type="checkbox" id="exampleCustomCheckbox6" label="Dammam" onChange={this.handleCheckBoxesChange} value="dammam" />
                                <CustomInput type="checkbox" id="exampleCustomCheckbox7" label="Jeddah" onChange={this.handleCheckBoxesChange} value="jeddah" />
                            </div>
                        </FormGroup>

                        <FormGroup>
                            <Label for="exampleCustomMutlipleSelect"><strong>By User</strong></Label>
                            <CustomInput type="select" id="exampleCustomMutlipleSelect" name="customSelect" multiple onChange={this.handleMultipleSelectChange} >
                                <option value="">Select</option>
                                {users}
                            </CustomInput>
                        </FormGroup>

                        <FormGroup>
                            <Label for="exampleCustomMutlipleSelect"><strong>By Tag</strong></Label>
                            <CustomInput type="select" id="exampleCustomMutlipleSelect" name="customSelect" multiple onChange={this.handleMultipleSelectChange} >
                                <option value="">Select</option>
                                {tagList}
                            </CustomInput>
                        </FormGroup>


                    </Form>
                    <Row >
                        <Col className="col-6" >
                            <Button outline color="primary" style={{ width: '100%' }} onClick={this.handleClearFilterClick}>Clear Filter</Button>
                        </Col>
                        <Col className="col-6" >
                            <Button outline color="success" style={{ width: '100%' }} onClick={this.handleApplyFilterClick} >Apply Filter</Button>
                        </Col>
                    </Row>

                </Card>
            </div>
        )
    }
}
