import React, { Component } from 'react'
import Filter from './Filter';
import ProductRow from './ProductRow';
import Cart from './Cart';
import NavBar from './NavBar'

import {
    Container,
    Row,
    Col
} from 'reactstrap';

export default class Main extends Component {
    state = {
        fullProducts: this.props.products,
        products: this.props.products,
        checkBoxesFilter: [],
        multipleSelectFilter: [],
        activePage: 'main',
        activeUser: '',
        cart: [],
        filter: {
            locations: [],
            categories: [],
            users: [],
            tags: []
        },
        addToProducts: {},
        addToCart: {},
        details: {}
    }

    filter = (filter1, filter2) => {
        const filteredProducts = [];
        let productsClone = this.state.fullProducts.slice()

        productsClone.forEach(product => {
            product.location.forEach(location => {
                if (filter1.includes(String(location).toLowerCase())) {
                    filteredProducts.push(product)
                }
            })
            product.categories.forEach(category => {
                if (filter1.includes(String(category).toLowerCase())) {
                    filteredProducts.push(product)
                }
            })
            product.tag.forEach(tag => {
                if (filter2.includes(String(tag))) {
                    filteredProducts.push(product)
                }
            })
            if (filter2.includes(String(product.user))) {
                filteredProducts.push(product)
            }

        })


        this.setState({ products: filteredProducts })
    }


    clearFilter = () => {
        this.setState({ products: this.state.fullProducts })
    }

    addToCart = (item) => {
        console.log('clicked');

        this.state.fullProducts.map((element, index) => {
            if (element.key === item) {
                this.state.cart.push(element);
            }
        })
        console.log(this.state.cart);


    }

    togglePage = () => {

        if (this.state.activePage == 'main') {
            this.setState({ activePage: 'cart' })
        }
    }

    render() {

        console.log(this.state.cart);

        const selectPage = () => {
            if (this.state.activePage === 'main') {
                return (
                    <Container fluid>
                        <Row>
                            <Col className="col-3 mt-5">
                                <Filter products={this.state.fullProducts} onFilter={this.filter} onClearFilter={this.clearFilter} />
                            </Col>
                            <Col className="col-8 mt-5">
                                <ProductRow products={this.state.products} onAddToCart={this.addToCart} />
                            </Col>
                        </Row>
                    </Container>
                )
            }
            else if (this.state.activePage === 'cart') {
                return <Cart {...this.state} cart={this.state.cart} rand="test" />
            }
            else if (this.state.activePage === 'user') {

            }
        }

        const page = selectPage();

        return (
            <div>
                <NavBar onTogglePage={this.togglePage} />
                {page}
            </div>
        )
    }
}
