import React, { Component } from 'react'
import ProductItem from './ProductItem';

import {
    Container,
    Row,
    Col
} from 'reactstrap';

export default class ProductRow extends Component {


    render() {


        var groupSize = 3;
        var products = this.props.products.map(element => {
            return (
                <Col className="col-4" >
                    <ProductItem title={String(element.title).split(' ')[0]} tag={element.tag} user={element.user} url={element.url} price={element.price} key={element.key} check={element.key} onAddToCart={this.props.onAddToCart} id={element.key} />
                </Col>
            )
        }).reduce(function (r, element, index) {
            // create element groups with size 3, result looks like:
            // [[elem1, elem2, elem3], [elem4, elem5, elem6], ...]
            index % groupSize === 0 && r.push([]);
            r[r.length - 1].push(element);
            return r;
        }, []).map(function (rowContent) {
            // surround every group with 'row'
            return <Row className="m-3">{rowContent}</Row>
        });
        // })


        return (
            <div>
                {products}
            </div >
        )
    }
}
