import React, {Component} from 'react';
import {
    FormGroup, Form, Label, Input,
    CardTitle, CardSubtitle, Button, Row, Col, Container
} from 'reactstrap';

class CartItems extends Component {

    render() {

    console.log(this.props);
    
    return (
        <div>
            <Container fluid>
                <Row style={{ width: "100%" }} >
                    <Col className="3">
                    <img width="50px" height="50px" src={this.props.url} />
                    </Col>
                    <Col className="6">
                    <h5>{this.props.title}</h5>
                    </Col>
                    <Col className="4">
                    </Col>
                    <Form inline>
                        <FormGroup>
                            <Button outline color="success">-</Button>
                            <Input type="text" name="orders" id="orders" placeholder="1" style={{ width: "15%", textAlign: "center"}} />
                            <Button outline color="success">+</Button>
                        </FormGroup>
                    </Form>
                </Row>
            </Container>
        </div>
    );
};
}

export default CartItems;
