import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class CartConfirm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false
        };

        this.toggle = this.toggle.bind(this);
    }

    handleAddToCartClick = (event) => {

    }

    toggle(event) {
        this.props.onAddToCart(event.target.id);
        this.setState(prevState => ({
            modal: !prevState.modal
        }));
    }

    render() {
        return (
            <div>
                <Button outline color="success" onClick={this.toggle} style={{ fontSize: '0.75em', width: '100%' }} >Add to Cart</Button>
                <Modal isOpen={this.state.modal} modalTransition={{ timeout: 700 }} backdropTransition={{ timeout: 1300 }}
                    toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
                    <ModalBody>
                        Your Item has been added to the shopping cart.
                    </ModalBody>
                    <ModalFooter>
                        <Button outline color="success" id={this.props.id} onClick={this.toggle}>OK</Button>{' '}
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}

export default CartConfirm;