import React, { Component } from 'react'
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    FormGroup,
    Label,
    Input,
    Form,
    Button,
    Container,
    Row,
    Col
} from 'reactstrap';

export default class NavBar extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }
    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    render() {

        return (
            <div>
                
                <Navbar color="light" light expand="md">
                    <NavbarBrand href="/">
                        <svg
                            width={50}
                            height={50}
                            xmlns="http://www.w3.org/2000/svg"
                            xmlnsXlink="http://www.w3.org/1999/xlink"
                        >
                            <image
                                xlinkHref="https://image.flaticon.com/icons/svg/1640/1640445.svg"
                                height={50}
                                width={50}
                            />
                        </svg>
                        <strong><em>Watermelon</em></strong>
                    </NavbarBrand>
                    <NavbarToggler onClick={this.toggle} />

                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="ml-auto" navbar>
                        <Form inline className="mr-2">
                        <FormGroup className="mb-2 mr-sm-2 mb-sm-0">
                            <Input type="search" name="search" id="serach" placeholder="What do you crave!" />
                        </FormGroup>
                        <Button color="success">Search</Button>
                    </Form>
                            <UncontrolledDropdown nav inNavbar>
                                <DropdownToggle nav caret>
                                    User
                                </DropdownToggle>
                                <DropdownMenu right>
                                    <DropdownItem>
                                    </DropdownItem>
                                    <DropdownItem>
                                        Manage Accounts
                                    </DropdownItem>
                                    <DropdownItem divider />
                                    <DropdownItem>
                                        Sign Out
                                    </DropdownItem>
                                </DropdownMenu>
                            </UncontrolledDropdown>

                            <UncontrolledDropdown nav inNavbar>
                                <DropdownToggle nav caret>
                                    Cart
                                </DropdownToggle>
                                <DropdownMenu right>
                                    <DropdownItem>
                                    </DropdownItem>
                                    <DropdownItem onClick={this.props.onTogglePage} >
                                        Checkout
                                    </DropdownItem>
                                    <DropdownItem divider />
                                    <DropdownItem>
                                        Manage Items
                                    </DropdownItem>
                                </DropdownMenu>
                            </UncontrolledDropdown>
                        </Nav>
                    </Collapse>
                </Navbar>

            </div>
        )
    }
}
