import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class Details extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            nestedModal: false,
            closeAll: false
        };

        this.toggle = this.toggle.bind(this);
        this.toggleNested = this.toggleNested.bind(this);
        this.toggleAll = this.toggleAll.bind(this);
    }

    toggle() {
        this.setState(prevState => ({
            modal: !prevState.modal
        }));
    }

    toggleNested() {
        this.setState({
            nestedModal: !this.state.nestedModal,
            closeAll: false
        });
    }

    toggleAll() {
        this.setState({
            nestedModal: !this.state.nestedModal,
            closeAll: true
        });
    }

    render() {
        return (
            <div>
                <Button outline color="success" style={{ fontSize: '0.75em', width: '100%' }} onClick={this.toggle}>{this.props.buttonLabel}More Details</Button>
                <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>{this.props.title}</ModalHeader>
                    <ModalBody>
                        <img width="100%" height="" src={this.props.url} alt="Card image cap" />
                        Description: {this.props.title}
                        <br />
                        <Modal isOpen={this.state.nestedModal} toggle={this.toggleNested} onClosed={this.state.closeAll ? this.toggle : undefined}>
                            <ModalHeader>Add item to cart</ModalHeader>
                            <ModalBody>Do you want to add {this.props.title} to your cart?</ModalBody>
                            <ModalFooter>
                                <Button outline color="success" onClick={this.toggleNested}>yes</Button>
                                <Button outline color="danger" onClick={this.toggleAll}>no</Button>
                            </ModalFooter>
                        </Modal>
                    </ModalBody>
                    <ModalFooter>
                    <Button outline color="success" onClick={this.toggleNested}>Add to Cart</Button>
                    <Button outline color="danger" onClick={this.toggle}>Close</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}

export default Details;