class Items extends React.Component {
    constructor(props) {
        super(props);
        // console.log(this.props);
        
        this.state = {
            firstStep: "first step here"
        };
    }
    render() {

        console.log('item');
        console.log(this.props[0]);
        console.log(this.props.props);
        
        
        const cartItems = this.props.props.map((item, index) => {
            return <CartItems url={item.url} title={item.title} />
        })

        return (
            <div>
                {cartItems}
            </div>
        )
    }
}

