class Cart extends React.Component {
    finishButtonClick(allStates) {
        console.log(allStates);
    }
    render() {
        
var steps = [
    // this step hasn't got a isValidated() function, so it will be considered to be true
    { stepName: "Items", component: <Items props={this.props.cart} />},
    // this step will be validated to false
    // this step will never be reachable because of the seconds isValidated() steps function that will always return false
    { stepName: "Payment Information", component: PaymentInfo },
    { stepName: "Shipping Information", component: ShippingInfo },
    { stepName: "Review", component: Review }
];

        
        return (
            <Container fluid style={{ marginTop: "15px", width: "90%" }}>
                <Row>
                    <Col xs={12} md={12} className="mr-auto ml-auto">
                        <ReactWizard
                            outline
                            steps={steps}
                            navSteps
                            title="Checkout"
                            headerTextCenter
                            validate
                            color="green"
                            finishButtonClick={this.finishButtonClick}
                        />
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default Cart;
