import React, { Component } from 'react'
import Details from './Details'
import CartConfirm from './CartConfirm'
import {
    Container,
    Form,
    FormGroup,
    Label,
    CustomInput,
    Card,
    CardBody,
    CardLink,
    CardText,
    CardTitle,
    CardSubtitle,
    CardHeader,
    CardFooter,
    Col,
    Row,
    Button
} from 'reactstrap';

export default class ProductItem extends Component {
    

    render() {

        
        return (
            <div>
                <Card className="p-1" color="light" style={{ textAlign: 'left' }}>
                    <Card>
                        <CardBody>
                            <CardHeader>{this.props.title}</CardHeader>
                        </CardBody>
                        <img width="100%" height="200px" src={this.props.url} alt="Card image cap" />
                        <CardBody>
                            <CardText>{`Price: ${this.props.price}`}</CardText>
                            <Row >
                                <Col className="col-6 p-1" >
                                    {/* <Button outline color="success" id={this.props.id} style={{ fontSize: '0.75em', width: '100%'}} onClick={this.handleAddToCartClick} >Add to Cart</Button> */}
                                    <CartConfirm id={this.props.id} onAddToCart={this.props.onAddToCart} />
                                </Col>
                                <Col className="col-6 p-1" >
                                <Details title={this.props.title} tag={this.props.tag} url={this.props.url} price={this.props.price}  />
                                </Col>
                            </Row>
                        </CardBody>
                        <CardFooter>{`#${this.props.tag} #${this.props.check}`}</CardFooter>
                    </Card>
                </Card>
            </div>
        )
    }
}
